.nav {
  width: 100%;
  height: 60px;
  margin-bottom: 17px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1173px;
    height: 100%;
    margin: 0 auto;
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__logo-pic {
    background-image: url("/images/afandiPic.svg");
    width: 60px;
    height: 60px;
  }

  &__logo-text {
    @include fontParams(18px, 17px, 500);
    font-family: 'Oswald', sans-serif;
  }

  &__links {
    display: flex;
    justify-content: space-around;
    width: 575px;
  }

  &__link {
    @include fontParams(14px, 17px, 400);
    color: $c-black;
    font-family: 'Montserrat', sans-serif;
  }

  &__lang-control {
    @include fontParams(14px, 16px, 400);
  }

  &__lang {
    font-family: 'Montserrat', sans-serif;
    color: $c-greyText;
  }
}
