.top {
  height: 50px;
  width: 100%;
  background-color: $c-blueLight;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1173px;
    height: 100%;
    margin: 0 auto;
  }

  &__tel-email {
    @include fontParams(13px, 16px, 400);
    display: flex;
    justify-content: space-between;
    font-family: $font-family-M;
    width: 440px;
    color: $c-black;
  }

  &__email {
    color: $c-black;
  }

  &__tel {
    color: $c-black;
  }

  &__social-medias {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 76px;
  }

  &__facebook {
    display: inline-block;
    background-image: url("/images/facebook.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
  }

  &__insta {
    display: inline-block;
    background-image: url("/images/instagram.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
  }

  &__telegram {
    display: inline-block;
    background-image: url("/images/telegram.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
  }
}
