.main-page {
  &__about {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    width: 1170px;
    margin: 0 auto 160px;
  }

  &__about-title {
    margin-bottom: 33px;
    text-align: center;
    font-weight: 500;
    font-size: 28px;
    line-height: 34px;

  }

  &__about-text {
    grid-column: 1/6;
  }

  &__about-content {
    margin-bottom: 32px;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.05em;
  }

  &__about-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
  }

  &__about-arrow {
    display: inline-block;
    width: 16px;
    height: 11px;
    margin-left: 10px;
    background-image: url('/images/Vector.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__about-pic {
    grid-column: 7/-1;
    width: 570px;
    height: 380px;
    border-radius: 18px;
    background-image: url('/images/group-of-people-working-out-business-plan-in-an-office\ 1.svg');
  }
}
